						alt=""
						v-if="playing && (song?.al?.id === n.id || song?.id === n.id)" />
					<i class="fa fa-play-circle" aria-hidden="true" v-else></i>
				</div>
			</li>
		</template>
		<!-- 歌单数据 -->
		<!-- <template v-else-if="chenglistsj[0]?.al?.name">
			<li
