				</div>
				<div class="ChengAudiolistr">
					<img src="@/assets/加载.gif" alt="" v-if="playing && (song?.al?.id === n.id || song?.id === n.id)" />
					<i class="fa fa-play-circle" aria-hidden="true" v-else></i>
				</div>
			</li>
		</template> -->
